* {
  margin: 0;
}
body{
  background-color: #f5f6fa;
}
.App{
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
}
.app__header{
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  justify-content: space-between;
}
.app__header > h1 {
  color: #FC3C3C;
  font-size: 2rem;
}
.app__dropdown{
  background-color: white;
}
.app__left{
  flex: 0.9;
}
.app__stats {
  display: flex;
  justify-content: space-between;
}
/* bagian yang kurang sedikit paham di dalam workshop */
.app__right {
  display: flex;
  flex-direction: column;
}
.app__right .MuiCardContent-root {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.app__graph{
  flex-grow: 1;
}
.app__graphTitle{
  margin-top: 20px;
  margin-bottom: 20px;
}
@media(max-width: 990px){
  .App {
    flex-direction: column;
  }
}
